<template>
	<div class="main">
		<div class="form-box">
			<el-form ref="form" :model="form" label-width="150px" :rules="rules" size="mini">
				<el-form-item label="" prop="goods_name">
					<el-input v-model="form.goods_name"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="form.goods_head_name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="cat_id">
					<el-select v-model="form.cat_id" placeholder="">
						<el-option-group
							v-for="group in cat_option"
							:key="group.label"
							:label="group.label">
							<el-option
								v-for="item in group.options"
								:key="item.value"
								:label="item.label"
								:value="item.value">
							</el-option>
						</el-option-group>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="supplier_id">
					<el-select v-model="form.supplier_id" placeholder="">
						<el-option
							v-for="item in supplier_options"
							:key="item.value"
							:label="item.label"
							:value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="click_type">
					<el-select v-model="form.click_type" placeholder="">
						<el-option
							v-for="item in click_type_options"
							:key="item.value"
							:label="item.label"
							:value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" v-show="form.click_type === 'external'">
					<el-input v-model="form.url"></el-input>
				</el-form-item>
				<el-form-item label=":">
					<el-input-number v-model="form.goods_sales_volume" :min="0" :precision="0"
						:controls="false"></el-input-number>
				</el-form-item>
				<el-form-item label=":">
					<el-input-number v-model="form.evaluate_count" :min="0" :precision="0"
						:controls="false"></el-input-number>
				</el-form-item>
				<el-form-item label=":">
					<el-input-number v-model="form.sort_order" :min="1" :precision="0" :max="999"
						:controls="false"></el-input-number>
					<el-alert
						title=""
						type="warning">
					</el-alert>
				</el-form-item>
				<el-form-item label="">
					<el-switch
						v-model="form.is_promote"
						active-text=""
						:active-value="1"
						:inactive-value="0">
					</el-switch>
				</el-form-item>
				<el-form-item v-show="form.is_promote === 1" label="()">
					<el-input-number v-model="form.promote_number" :min="1" :precision="0"></el-input-number>
					<el-alert
						title=" - sku = "
						type="warning">
					</el-alert>
				</el-form-item>
				<el-form-item v-show="form.is_promote === 1" label="" prop="promote_date">
					<el-date-picker
						v-model="form.promote_date"
						type="datetimerange"
						value-format="timestamp"
						range-separator=""
						start-placeholder=""
						end-placeholder="">
					</el-date-picker>
					<el-alert
						title=""
						type="warning">
					</el-alert>
				</el-form-item>
				<el-form-item label="">
					<el-switch
						v-model="form.is_best"
						active-text=""
						:active-value="1"
						:inactive-value="0">
					</el-switch>
				</el-form-item>
				<el-form-item label="">
					<el-switch
						v-model="form.is_new"
						active-text=""
						:active-value="1"
						:inactive-value="0">
					</el-switch>
				</el-form-item>
				<el-form-item label="">
					<el-switch
						v-model="form.is_hot"
						active-text=""
						:active-value="1"
						:inactive-value="0">
					</el-switch>
				</el-form-item>
				<el-form-item label="">
					<el-switch
						v-model="form.is_on_sale"
						active-text=""
						:active-value="1"
						:inactive-value="0">
					</el-switch>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="form.keywords" prop="keywords"></el-input>
					<el-alert
						title=" | 4 |"
						type="warning">
					</el-alert>
				</el-form-item>
				<el-form-item label="" prop="goods_brief">
					<el-input v-model="form.goods_brief" type="textarea"></el-input>
					<el-alert
						title=" | "
						type="warning">
					</el-alert>
				</el-form-item>
				<el-form-item label="">
					<el-upload
						:action="$store.state.api_url + 'cms/cms_upload_goods_img'"
						:on-success="uploadSuccess"
						:on-remove="remove"
						:limit="1"
						accept="image/jpeg,image/gif,image/png"
						name='portrait_img'
						:file-list="file_list"
						:before-upload="beforeAvatarUpload"
						list-type="picture-card">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-alert
						title=","
						type="warning">
					</el-alert>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>
<script>
    export default {
        data() {
            var checkPromoteDate = (rule, value, callback) => {
                if (this.form.is_promote === 1 && this.form.promote_date === null) {
                    callback(new Error(''));
                }
            };
            return {
                form: {
                    goods_name: '',
                    goods_head_name: '',
                    cat_id: '',
                    supplier_id: 1,
                    is_promote: 0,
                    goods_sales_volume: 0,
                    evaluate_count: 0,
                    promote_number: 0,
                    promote_date: null,
                    is_best: 0,
                    is_new: 0,
                    is_hot: 0,
                    is_on_sale: 1,
                    keywords: '',
                    goods_brief: '',
                    goods_img: '',
                    original_img: '',
                    click_type: 'inside',
                    url: '',
                    sort_order: 10
                },
                file_list: [],
                rules: {
                    goods_name: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 1, max: 100, message: ' 1  100 ', trigger: 'blur'}
                    ],
                    cat_id: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    supplier_id: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    click_type: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    keywords: [
                        {min: 1, max: 100, message: ' 1  100 ', trigger: 'blur'}
                    ],
                    goods_brief: [
                        {min: 1, max: 100, message: ' 3  100 ', trigger: 'blur'}
                    ],
                    promote_date: [
                        {validator: checkPromoteDate, trigger: 'blur'}
                    ]
                },
                supplier_options: [],
                click_type_options: [
                    {
                        value: "inside",
                        label: ""
                    },
                    {
                        value: "external",
                        label: ""
                    }
                ],
            };
        },
        computed: {
            cat_option: {
                get: function () {
                    let result = [];
                    if (this.$store.state.cat_list.length > 0) {
                        this.$store.state.cat_list.forEach(item => {
                            this.$store.state.cat_list.forEach(item2 => {
                                if (item.parent_id == item2.cat_id) {
                                    /**/
                                    let result_flag = true;
                                    result.forEach((item3, i3) => {
                                        if (item3.label === item2.cat_name) {
                                            result[i3].options.push({value: item.cat_id, label: item.cat_name});
                                            result_flag = false;
                                        }
                                    });
                                    if (result_flag) {
                                        let option = {};
                                        option.label = item2.cat_name;
                                        option.options = [];
                                        option.options.push({value: item.cat_id, label: item.cat_name});
                                        result.push(option);
                                    }
                                }

                            })
                        })
                    }
                    return result;
                }
            }
        },
        props: ["edit_type"],
        created() {
            if (this.$store.state.cat_list.length < 1) {
                this.getCatList();
            }
            this.getSupplier();
            if (this.edit_type === 'upd' || this.edit_type === 'copy') {
                this.form = this.$store.state.goods_info;
                if (this.form.is_promote === 1) {
                    this.$set(this.form, 'promote_date', new Array(new Date(this.$store.state.goods_info.promote_start_date * 1000).getTime(), new Date(this.$store.state.goods_info.promote_end_date * 1000).getTime()));
                }
                let file_list_object = {
                    name: this.form.goods_img,
                    url: this.form.goods_img,
                    original_url: this.form.original_img
                };
                this.file_list.push(file_list_object);
            }
        },
        methods: {
            /**/
            getCatList() {
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('cms_get_cat_list', {
                    admin_token: this.$store.state.gl_cms_token,
                })
                    .then((msg) => {
                        this.$set(this.$store.state, 'cat_list', msg);
                        m_loading.close();
                    })
            },
            /**/
            getSupplier() {
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('cms_get_all_supplier', {
                    admin_token: this.$store.state.gl_cms_token,
                })
                    .then((msg) => {
                        msg.forEach(item => {
                            this.supplier_options.push({
                                label: item.supplier_name,
                                value: item.id
                            })
                        });
                        m_loading.close();
                    })
            },
            /**/
            beforeAvatarUpload(file) {
                let check_flag;
                const isLt1M = file.size / 1024 / 1024 < 1;
                if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
                    this.$message.error(' JPG PNG!');
                    check_flag = false;
                }
                if (!isLt1M) {
                    this.$message.error(' 1MB!');
                    check_flag = false;
                }
                return check_flag;
            },
            /**/
            uploadSuccess(response) {
                let img_info = {
                    name: response.goods_img,
                    url: response.goods_img,
                    original_url: response.original_img
                };
                this.file_list.push(img_info);
            },
            /**/
            remove(file) {
                //  file_list
                if (this.file_list.length > 0 && file.status === 'success') {
                    this.file_list.forEach((item, i) => {
                        if (item.name === file.name) {
                            this.file_list.splice(i, 1);
                        }
                    });
                }
            },
            /**/
            checkForm() {
                let check_flag = true;
                this.$refs['form'].validate((valid) => {
                    if (!valid) {
                        this.$message.error('');
                        check_flag = false;
                    }
                });
                return check_flag;
            }
        }
        ,
    }
    ;
</script>
<style lang="scss" scoped>
	.main {
		.form-box {
			width: 60%;
		}
	}
</style>