<template>
  <div class="main">
    <div class="table-box">
      <el-table
          :data="tableData"
          height="850px"
          stripe
          border
          style="width: 100%">
        <el-table-column
            prop="order_sn"
            label=""
            width="180">
        </el-table-column>
        <el-table-column
            label=""
            width="80">
          <template slot-scope="scope">
            <p :style="{color:scope.row.is_allow===0?'red':'black'}">{{serverOrderStatus[scope.row.is_allow]}}</p>
          </template>
        </el-table-column>
        <el-table-column
            prop="allow_time"
            label=""
            width="200">
        </el-table-column>
        <el-table-column
            prop="create_time"
            label=""
            width="180">
        </el-table-column>
        <el-table-column
            prop="user_id"
            label="id"
            width="100">
        </el-table-column>
        <el-table-column
            prop="after_sale_cause"
            label=""
            width="180">
        </el-table-column>
        <el-table-column
            prop="after_sale_type"
            label=""
            width="180">
        </el-table-column>
        <el-table-column
            prop="after_sale_text"
            label=""
            width="180">
        </el-table-column>
        <el-table-column
            fixed="right"
            label=""
            width="200">
          <template slot-scope="scope">
            <el-button type="text" size="small" v-if="tableData[scope.$index].is_allow === 0"
                       @click.native.prevent="afterOrderRefund(scope.$index, tableData)">
            </el-button>
            <el-button type="text" size="small" @click.native.prevent="seeOrder(scope.$index, tableData)">
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="my-el-pagination">
      <el-pagination
          @size-change="getList"
          @current-change="getList"
          :current-page.sync="page"
          :page-sizes="[10, 20, 30, 40]"
          :page-size.sync="limit"
          layout="sizes, prev, pager, next"
          :total="count">
      </el-pagination>
    </div>
  </div>
</template>
<script>
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                count: 0,
                truthAuthCode: "gl888888",
                authCode: "",
                maxServiceTimeInterval: 1296000000,//15
                //10
                serverOrderStatus: {
                    0: "",
                    1: ""
                },
            };
        },
        computed: {},
        created() {
            this.getList();
        },
        methods: {
            getList() {
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('cms_get_after_sale_list', {
                    admin_token: this.$store.state.gl_cms_token,
                    page: this.page,
                    limit: this.limit,
                })
                    .then((msg) => {
                        this.$set(this, 'tableData', msg.list);
                        this.$set(this, 'count', msg.count);
                        m_loading.close();
                    })
            }
            /**/
            , afterOrderRefund(index, rows) {
                let order_sn = rows[index].order_sn;
                let after_sale_id = rows[index].id;
                let submitTime = new Date(rows[index].create_time).getTime();
                let nowTime = new Date().getTime();
                if (nowTime - submitTime >= this.maxServiceTimeInterval) {
                    this.$prompt('', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        inputPattern: new RegExp('^(' + this.truthAuthCode + ')$'),
                        inputErrorMessage: ''
                    }).then(() => {
                        this.orderRefund(order_sn, after_sale_id);
                    });
                } else {
                    this.orderRefund(order_sn, after_sale_id);
                }


            }
            /**/
            , orderRefund(order_sn, after_sale_id) {
                // let order_sn = rows[index].order_sn;
                // let after_sale_id = rows[index].id;
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$post('cms_order_refund', {
                        admin_token: this.$store.state.gl_cms_token,
                        order_sn: order_sn,
                        after_sale_id: after_sale_id
                    })
                        .then((msg) => {
                            if (msg) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                this.getList();
                            }
                            m_loading.close();
                        })
                });
            }
            /**/
            , seeOrder(index, rows) {
                let order_sn = rows[index].order_sn;
                this.$router.push({
                    path: '/seeOrder',
                    query: {
                        order_sn: order_sn
                    }
                });

            }
        },
    };
</script>
<style lang="scss" scoped>
  .my-el-pagination {
    z-index: 10;
    position: fixed;
    bottom: 0;
  }
</style>