<template>
	<div class="main">
		<div class="table-box">
			<el-table
				:data="tableData"
				height="850px"
				stripe
				border
				style="width: 100%">
				<el-table-column
					prop="make_order_sn"
					label=""
					width="180">
				</el-table-column>
				<el-table-column
					prop="make_state"
					label=""
					width="80">
				</el-table-column>
				<el-table-column
					prop="goods_id"
					label="id"
					width="80">
				</el-table-column>
				<el-table-column
					show-overflow-tooltip
					prop="goods_name"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					prop="sku_desc"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					prop="goods_number"
					label=""
					width="80">
				</el-table-column>
				<el-table-column
					prop="make_order_price"
					label=""
					width="100">
				</el-table-column>
				<el-table-column
					prop="make_name"
					label=""
					width="100">
				</el-table-column>
				<el-table-column
					prop="make_phone"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					prop="make_time"
					label=""
					width="180">
				</el-table-column>
				<el-table-column
					prop="make_address"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					prop="make_remake"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					fixed="right"
					label=""
					width="180">
					<template slot-scope="scope">
						<el-button type="text" size="small"
							v-if="tableData[scope.$index].make_state === 0"
							@click.native.prevent="handleOrder(scope.$index, tableData)">
						</el-button>
						<el-button type="text" size="small"
							@click.native.prevent="delOrder(scope.$index, tableData)">
						</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<div class="my-el-pagination">
			<el-pagination
				@size-change="getList"
				@current-change="getList"
				:current-page.sync="page"
				:page-sizes="[10, 20, 30, 40]"
				:page-size.sync="limit"
				layout="sizes, prev, pager, next"
				:total="count">
			</el-pagination>
		</div>
	</div>
</template>
<script>
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                count: 0,
            };
        },
        computed: {},
        created() {
            this.getList();
        },
        methods: {
            getList() {
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('cms_get_make_order_list', {
                    admin_token: this.$store.state.gl_cms_token,
                    page: this.page,
                    limit: this.limit,
                })
                    .then((msg) => {
                        this.$set(this, 'tableData', msg.list);
                        this.$set(this, 'count', msg.count);
                        m_loading.close();
                    })
            }
            , delOrder(index, rows) {
                let make_order_sn = rows[index].make_order_sn;
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$post('cms_del_make_order', {
                        admin_token: this.$store.state.gl_cms_token,
                        make_order_sn: make_order_sn
                    })
                        .then((msg) => {
                            if (msg) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                //
                                rows.splice(index, 1);
                            }
                            m_loading.close();
                        })
                });
            }
            , handleOrder(index, rows) {
                let make_order_sn = rows[index].make_order_sn;
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$post('cms_handle_make_order', {
                        admin_token: this.$store.state.gl_cms_token,
                        make_order_sn: make_order_sn
                    })
                        .then((msg) => {
                            if (msg) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                //
                                //rows.splice(index, 1);
                                this.getList();
                            }
                            m_loading.close();
                        })
                });
            }
        },
    };
</script>
<style lang="scss" scoped>
	.my-el-pagination {
		z-index: 10;
		position: fixed;
		bottom: 0;
	}
</style>