<template>
	<div class="main">
		<div class="button-box">
			<el-button type="primary" @click="$router.push('/CreateShareUrl')">
				
			</el-button>
		</div>
		<el-table
			:data="tableData"
			height="850px"
			stripe
			border
			style="width: 100%">
			<el-table-column
				prop="user_id"
				label=""
				width="100">
			</el-table-column>
			<el-table-column
				prop="create_time"
				label=""
				width="200">
			</el-table-column>
			<el-table-column
				show-overflow-tooltip
				prop="count_day"
				label=""
				width="150">
			</el-table-column>
			<el-table-column
				show-overflow-tooltip
				prop="continuity_day"
				label=""
				width="150">
			</el-table-column>
			<el-table-column
				prop="avg_time"
				label=""
				width="200">
			</el-table-column>
			<el-table-column
				show-overflow-tooltip
				prop="count_share"
				label=""
				width="250">
			</el-table-column>
		</el-table>
		<div class="my-el-pagination">
			<el-pagination
				@size-change="getList"
				@current-change="getList"
				:current-page.sync="page"
				:page-sizes="[10, 20, 30, 40,80,160]"
				:page-size.sync="limit"
				layout="sizes, prev, pager, next"
				:total="count">
			</el-pagination>
		</div>
	</div>
</template>
<script>
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 20,
                count: 0,
            };
        },
        created() {
            this.getList();
        },
        methods: {
            getList() {
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('article_sign_get_user_list', {
                    admin_token: this.$store.state.gl_cms_token,
                    page: this.page,
                    limit: this.limit,
                })
                    .then((msg) => {
                        this.$set(this, 'tableData', msg.list);
                        this.$set(this, 'count', msg.count);
                        m_loading.close();
                    })
            }
        },
    };
</script>
