<template>
  <div class="orderListMain">
    <el-container>
      <el-main>
        <div class="form-box">
          <el-form ref="form" :model="form" label-width="120px" size="mini" inline>
            <el-form-item label="">
              <el-input v-model="form.order_sn"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.logistics_address_name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.logistics_address_phone"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.order_state" placeholder="">
                <el-option
                    v-for="item in order_state_option"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.cat_id" placeholder="">
                <el-option-group
                    v-for="group in cat_option"
                    :key="group.label"
                    :label="group.label">
                  <el-option
                      v-for="item in group.options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                  </el-option>
                </el-option-group>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.son_into_type" placeholder="">
                <el-option
                    v-for="item in son_into_type_option"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="goSearch(true,page,limit)"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="button-box">
          <el-form ref="form" :model="importForm" label-width="120px" size="mini" inline>
            <el-form-item label="">
              <el-select v-model="importForm.order_state" placeholder="">
                <el-option
                    v-for="item in order_state_option"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.cat_id" placeholder="">
                <el-option-group
                    v-for="group in cat_option"
                    :key="group.label"
                    :label="group.label">
                  <el-option
                      v-for="item in group.options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                  </el-option>
                </el-option-group>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="importForm.son_into_type" placeholder="">
                <el-option
                    v-for="item in son_into_type_option"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker
                  v-model="importForm.date"
                  type="daterange"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder="">
              </el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="importOrder"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="table-box">
          <el-table
              :data="tableData"

              stripe
              border
              style="width: 100%">
            <el-table-column
                prop="order_sn"
                label=""
                width="180">
            </el-table-column>
            <el-table-column
                prop="son_into_type_name"
                label=""
                width="120">
            </el-table-column>
            <el-table-column
                prop="cat_name"
                label=""
                width="120">
            </el-table-column>
            <el-table-column
                prop="order_price"
                label=""
                width="80">
            </el-table-column>
            <el-table-column
                prop="create_time"
                label=""
                width="180">
            </el-table-column>
            <el-table-column
                label=""
                width="120">
              <template slot-scope="scope">
                <p>{{order_state_info[scope.row.order_state]}}</p>
              </template>
            </el-table-column>
            <el-table-column
                prop="logistics_name"
                label=""
                width="100">
            </el-table-column>
            <el-table-column
                prop="logistics_tel"
                label=""
                width="180">
            </el-table-column>

            <el-table-column
                fixed="right"
                label=""
                width="250">
              <template slot-scope="scope">
                <el-button type="text" size="small"
                           @click.native.prevent="seeOrder(scope.$index, tableData)">
                </el-button>
                <el-button type="text" size="small" v-if="tableData[scope.$index].order_state === 1"
                           @click.native.prevent="callOrder(scope.$index, tableData)">
                </el-button>
                <el-button type="text" size="small" v-if="tableData[scope.$index].order_state === 0"
                           @click.native.prevent="delOrder(scope.$index, tableData)">
                </el-button>
                <el-button type="text" size="small"
                           @click.native.prevent="payQuery(scope.$index, tableData)">
                </el-button>
                <el-button type="text" size="small"
                           @click.native.prevent="toEditLogistics(scope.$index, tableData)">
                </el-button>
                <el-button type="text" size="small"
                           v-if="tableData[scope.$index].order_state === 2 ||tableData[scope.$index].order_state === 4"
                           @click.native.prevent="changeOrderStateAfterSale(scope.$index, tableData)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-main>
      <el-footer>
        <div class="my-el-pagination">
          <el-pagination
              v-if="showPagination"
              @size-change="(pageSize)=>{
              goSearch(true,1,pageSize)
          }"
              @current-change="(page)=>{
              goSearch(false,page,this.limit)
          }"
              :current-page="page"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="limit"
              layout="sizes, prev, pager, next"
              :total="count">
          </el-pagination>
        </div>
      </el-footer>
    </el-container>
  </div>
</template>
<script>
    import zipcelx from 'zipcelx';

    export default {
        name: "OrderList",
        data() {
            return {
                showPagination: true,
                form: {
                    order_sn: '',
                    logistics_address_name: '',
                    logistics_address_phone: '',
                    order_state: 'all',
                    son_into_type: "all",
                    car_id: 0,
                },
                importForm: {
                    order_state: 'all',
                    son_into_type: "all",
                    date: "",
                    car_id: 0,
                },
                order_state_option: [
                    {value: 'all', label: ''},
                    {value: 'pending_pay', label: ''},
                    {value: 'pending_delivery', label: ''},
                    {value: 'pending_take', label: ''},
                    {value: 'pending_service', label: ''},
                ],
                son_into_type_option: [
                    {value: 'all', label: ''},
                    {value: 'wx', label: ''},
                    {value: 'abc_wx', label: ''},
                    {value: 'abc_app', label: 'app'},
                    {value: 'ios', label: 'ios'},
                    {value: 'android', label: 'android'},
                    {value: 'pc', label: 'pc'},
                ],
                tableData: [],
                page: 1,
                limit: 10,
                count: 0,
                order_state_info: {
                    0: "",
                    1: "",
                    2: "",
                    3: "",
                    4: "",
                    5: "",
                    6: "",
                    7: "",
                    8: "",
                },
            };
        },
        computed: {
            cat_option: {
                get: function () {
                    let result = [];
                    if (this.$store.state.cat_list.length > 0) {
                        this.$store.state.cat_list.forEach(item => {
                            this.$store.state.cat_list.forEach(item2 => {
                                if (item.parent_id == item2.cat_id) {
                                    /**/
                                    let result_flag = true;
                                    result.forEach((item3, i3) => {
                                        if (item3.label === item2.cat_name) {
                                            result[i3].options.push({value: item.cat_id, label: item.cat_name});
                                            result_flag = false;
                                        }
                                    });
                                    if (result_flag) {
                                        let option = {};
                                        option.label = item2.cat_name;
                                        option.options = [];
                                        option.options.push({value: item.cat_id, label: item.cat_name});
                                        result.push(option);
                                    }
                                }

                            })
                        })
                    }
                    result.push({
                        label: '',
                        options: [{
                            value: 0,
                            label: ''
                        }]
                    });
                    return result;
                }
            }
        },
        created() {
            if (this.$store.state.cat_list.length < 1) {
                this.$store.dispatch("getCatList", this.$store.state.gl_cms_token);
            }
        },
        beforeRouteEnter(to, from, next) {
            //  confirm 
            //  `this`
            // 
            next(vm => {
                vm.urlUpdate(to);
            });
        },
        beforeRouteUpdate(to, from, next) {
            this.urlUpdate(to);
            next();
        },
        methods: {
            urlUpdate(url) {
                if (!url || !url.query || Object.keys(url.query).length === 0) {
                    //this.form = Object.create(originForm);
                    this.page = 1;
                    this.limit = 10;
                } else {
                    this.$set(this, 'page', parseInt(url.query.page));
                    //this.page = parseInt(url.query.page);
                    this.$set(this, 'limit', parseInt(url.query.limit));
                    for (let variable in url.query) {
                        if (this.form.hasOwnProperty(variable) && url.query[variable]) {
                            this.form[variable] = url.query[variable];
                        }
                    }
                }
                this.getList();
            },
            getList() {
                this.showPagination = false;
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('cms_get_order_list', {
                    admin_token: this.$store.state.gl_cms_token,
                    page: this.page,
                    limit: this.limit,
                    order_sn: this.form.order_sn.replace(/\s+/g, ""),
                    logistics_address_name: this.form.logistics_address_name.replace(/\s+/g, ""),
                    logistics_address_phone: this.form.logistics_address_phone.replace(/\s+/g, ""),
                    order_state: this.form.order_state.replace(/\s+/g, ""),
                    son_into_type: this.form.son_into_type.replace(/\s+/g, ""),
                    cat_id: this.form.cat_id,
                })
                    .then((msg) => {
                        this.$set(this, 'tableData', msg.list);
                        this.$set(this, 'count', msg.count);
                        this.showPagination = true;
                        m_loading.close();
                    })
            },
            goSearch(isInit, page, limit) {
                let params = Object.create(this.form);
                Object.defineProperty(params, 'page', {
                    value: page,
                    enumerable: true
                });
                Object.defineProperty(params, 'limit', {
                    value: limit,
                    enumerable: true,
                });
                this.$router.push({path: '/OrderList', query: params});
            }
            /**/
            , importOrder() {
                if (!this.importForm.date) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                    return false;
                }
                // ms
                //console.log(typeof(Date.parse(this.importForm.date[0])));
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('cms_import_order_list', {
                    admin_token: this.$store.state.gl_cms_token,
                    order_state: this.importForm.order_state.replace(/\s+/g, ""),
                    son_into_type: this.importForm.son_into_type.replace(/\s+/g, ""),
                    start_time: Date.parse(this.importForm.date[0]) / 1000,
                    end_time: Date.parse(this.importForm.date[1]) / 1000 + 86400,
                    cat_id: this.importForm.car_id,
                })
                    .then((msg) => {
                        let orderData = this.frontImportOrder(msg.list);
                        let config = {
                            filename: 'importOrderList',
                            sheet: {
                                data: orderData,
                            }
                        };
                        zipcelx(config);
                        m_loading.close();

                    })
            }
            /**/
            , frontImportOrder(orderDataList) {

                /**/
                let result = [];
                result.push([
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},
                    {value: "", type: "string"},]);
                if (orderDataList instanceof Array && orderDataList.length > 0) {
                    orderDataList.forEach((item) => {
                        result.push([
                            {value: item.order_sn, type: "string"},
                            {value: this.order_state_info[item.order_state], type: "string"},
                            {value: item.create_time, type: "string"},
                            {value: item.pay_time, type: "string"},
                            {value: item.pay_name + item.bystages_val, type: "string"},
                            {value: item.original_order_price, type: "string"},
                            {
                                value: (parseFloat(item.original_order_price) - parseFloat(item.after_using_coupon_price)).toFixed(2),
                                type: "string"
                            },
                            {value: item.order_price, type: "string"},
                            {value: item.logistics_name, type: "string"},
                            {value: item.logistics_tel, type: "string"},
                            {value: item.logistics_address, type: "string"},
                            {value: item.cat_name, type: "string"},
                            {value: item.son_into_type_name, type: "string"},
                        ])
                    })
                }

                return result;
            }
            /**/
            , callOrder(index, rows) {
                let order_sn = rows[index].order_sn;
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$post('cms_call_order', {admin_token: this.$store.state.gl_cms_token, order_sn: order_sn})
                        .then((msg) => {
                            if (msg) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                this.getList();
                            }
                            m_loading.close();
                        })
                });
            }
            /**/
            , delOrder(index, rows) {
                let order_sn = rows[index].order_sn;
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$post('cms_del_order', {admin_token: this.$store.state.gl_cms_token, order_sn: order_sn})
                        .then((msg) => {
                            if (msg) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                this.getList();
                            }
                            m_loading.close();
                        })
                });
            }
            /**/
            , payQuery(index, rows) {
                let order_sn = rows[index].order_sn;
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$fetch('cms_pay_query', {admin_token: this.$store.state.gl_cms_token, order_sn: order_sn})
                        .then((msg) => {
                            m_loading.close();
                            this.$alert(msg, '', {
                                confirmButtonText: ''
                            });
                        })
                });
            }
            /**/
            , changeOrderStateAfterSale(index, rows) {
                let order_sn = rows[index].order_sn;
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$post('cms_change_order_state_after_sale', {
                        admin_token: this.$store.state.gl_cms_token,
                        order_sn: order_sn
                    })
                        .then((msg) => {
                            m_loading.close();
                            if (msg) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                            }

                        })
                });
            }
            /**/
            , toEditLogistics(index, rows) {
                let order_info = rows[index];
                this.$router.push({
                    path: '/EditLogistics',
                    query: {
                        order_sn: order_info.order_sn,
                    }
                });
            }
            /**/
            , seeOrder(index, rows) {
                //let order_info = JSON.stringify(rows[index]);
                this.$router.push({
                    path: '/seeOrder',
                    query: {
                        order_sn: rows[index].order_sn
                    }
                });

            }
        },
    };
</script>
<style lang="scss" scoped>
  .orderListMain {
    /*.form-box {
      margin-top: 20px;
    }

    .my-el-pagination {
      z-index: 10;
      position: fixed;
      bottom: 0;
    }*/
  }

</style>