<template>
	<div class="main">
		<div class="button-box">
			<el-button type="primary"
				@click="$router.push({ path: 'EditCoupon', query: { edit_type: 'add',coupon_id:'0' }})">
				
			</el-button>
		</div>
		<div class="table-box">
			<el-table
				:data="tableData"
				height="850px"
				stripe
				border
				style="width: 100%">
				<el-table-column
					prop="coupon_name"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					prop="grant_type"
					label=""
					width="100">
				</el-table-column>
				<el-table-column
					prop="into_type"
					label=""
					width="50">
				</el-table-column>
				<el-table-column
					show-overflow-tooltip
					prop="coupon_desc"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					prop="start_grant_time"
					label=""
					width="200">
				</el-table-column>
				<el-table-column
					prop="end_grant_time"
					label=""
					width="180">
				</el-table-column>
				<el-table-column
					prop="start_use_time"
					label=""
					width="200">
				</el-table-column>
				<el-table-column
					prop="end_use_time"
					label=""
					width="200">
				</el-table-column>
				<el-table-column
					prop="coupon_number"
					label=""
					width="100">
				</el-table-column>
				<el-table-column
					prop="coupon_remainder_number"
					label=""
					width="150">
				</el-table-column>
				<el-table-column
					prop="found_sum"
					label=""
					width="100">
				</el-table-column>
				<el-table-column
					prop="cut_sum"
					label=""
					width="100">
				</el-table-column>
				<el-table-column
					fixed="right"
					label=""
					width="180">
					<template slot-scope="scope">
						<el-button type="text" size="small" @click.native.prevent="delCoupon(scope.$index, tableData)">
							
						</el-button>
						<el-button type="text" size="small" @click.native.prevent="editCoupon(scope.$index, tableData)">
							
						</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>
		<div class="my-el-pagination">
			<el-pagination
				@size-change="getList"
				@current-change="getList"
				:current-page.sync="page"
				:page-sizes="[10, 20, 30, 40]"
				:page-size.sync="limit"
				layout="sizes, prev, pager, next"
				:total="count">
			</el-pagination>
		</div>
	</div>
</template>
<script>
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                count: 0,
            };
        },
        computed: {},
        created() {
            this.getList();
        },
        methods: {
            getList() {
                let m_loading = this.$loading({
                    text: ''
                });
                this.$fetch('cms_get_coupon_list', {
                    admin_token: this.$store.state.gl_cms_token,
                    page: this.page,
                    limit: this.limit,
                })
                    .then((msg) => {
                        this.$set(this, 'tableData', msg.list);
                        this.$set(this, 'count', msg.count);
                        m_loading.close();
                    })
            }
            , editCoupon(index, rows) {
                this.$router.push({path: 'EditCoupon', query: {type: 'upd', coupon_info: JSON.stringify(rows[index])}})
            }
            , delCoupon(index, rows) {
                let coupon_id = rows[index].coupon_id;
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let m_loading = this.$loading({
                        text: ''
                    });
                    this.$post('cms_del_coupon', {admin_token: this.$store.state.gl_cms_token, coupon_id: coupon_id})
                        .then((msg) => {
                            if (msg) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                                //
                                rows.splice(index, 1);
                            }
                            m_loading.close();
                        })
                });
            }
        },
    };
</script>
<style lang="scss" scoped>
	.button-box {
		margin: 10px;
	}

	.my-el-pagination {
		z-index: 10;
		position: fixed;
		bottom: 0;
	}
</style>