<template>
  <div class="main">
    <div class="form-box">
      <el-form ref="form" :model="form" label-width="80px" size="mini">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="form.logistics_sn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cat_name">
          <el-input v-model="form.order_sn" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="cat_name">
          <el-input v-model="form.order_state" disabled></el-input>
          <el-alert
              title="012345678"
              type="success">
          </el-alert>
        </el-form-item>
        <el-form-item label="" prop="cat_name">
          <el-input v-model="form.deliver_goods_time" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="cat_id">
          <el-select v-model="form.logistics_code" placeholder="">
            <el-option
                v-for="item in option"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="cat_name">
          <el-input v-model="form.logistics_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cat_name">
          <el-input v-model="form.logistics_tel"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cat_name">
          <el-input v-model="form.logistics_address"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="danger" v-if="form.order_state === 2" @click="deliveryOrder"></el-button>
          <el-button type="primary" @click="updLogistics"></el-button>
          <el-alert
              title=""
              type="warning">
          </el-alert>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                form: {},
                option: [
                    {label: '', value: 'shunfeng'},
                    {label: '', value: 'youzhenxiaobao'},
                    {label: '', value: 'bestex'},
                    {label: '', value: 'ems'},
                ],
            };
        },
        computed: {},
        created() {
            this.order_sn = this.$route.query.order_sn;
            this.getOrderInfo(this.order_sn);


        },
        methods: {
            getOrderInfo(order_sn) {
                this.$fetch('cms_get_order_info', {
                    admin_token: this.$store.state.gl_cms_token,
                    order_sn: order_sn
                })
                    .then((msg) => {
                        if (msg) {
                            this.form = msg;
                        }
                    })
            },
            /**/
            deliveryOrder() {
                /* (new CurrencyValidate())
                ->myGoCheck(['order_sn', 'logistics_code', 'logistics_name', 'logistics_tel', 'logistics_address', 'logistics_sn'], 'require');*/
                let m_loading = this.$loading({
                    text: ''
                });
                let post_info = this.form;
                post_info.admin_token = this.$store.state.gl_cms_token;
                this.$post('cms_delivery_order', post_info)
                    .then((msg) => {
                        if (msg) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.$router.go(-1);
                        }
                        m_loading.close();
                    });


            }
            /**/
            , updLogistics() {
                let m_loading = this.$loading({
                    text: ''
                });
                let post_info = this.form;
                post_info.admin_token = this.$store.state.gl_cms_token;
                this.$post('cms_upd_logistics', post_info)
                    .then((msg) => {
                        if (msg) {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.$router.go(-1);
                        }
                        m_loading.close();
                    });


            }
        },
    };
</script>

<style lang="scss" scoped>
  .form-box {
    width: 70%;
    margin-top: 50px;
  }
</style>